<template>
  <div  class="detailimage-box">
        <div class="detailimage-first">
              <img :src="detailimages.img" alt="">
              <p>{{detailimages.title}}</p>
              <div>
                  <div class="detailimage-first-one">
                      <i>&#65509;</i>
                      <span>{{detailimages.price}}</span>
                  </div>
                  <div class="detailimage-first-two">
                      <p  class="detailimage-first-two-one">
                          <i>&#65509;</i>{{detailimages.orgPrice}}
                      </p>
                      <p class="detailimage-first-two-two" >

                      </p>
                  </div>
                 
              </div>
               <p>免邮费</p>
        </div>
      <AssessInt :detailcomments="detailassess"/>
  </div>
</template>
<script>
import AssessInt from "./AssessInt"
export default {
    props:{
        detailimages:{
            type:Object,
            default:{}
        },
        detailassess:{
            type:Object,
            default:{}
        }
    },
    components:{
      AssessInt
    }

}
</script>
<style  lang="less">
@import url("../less/less-all.less");
 .detailimage{
     &-box{
         width:100%;
         
       .detailimage-first{
           padding:20px 30px 20px 25px;
           width:92%; 
           border-bottom:@border @gray;
           margin-bottom:30px;
          background:white;
           img{
               width:90%;
               margin-left:40px;
           }
        //    >p{ 
        //          margin-top:10px;
        //          width:95%;
        //          height:80px;
        //          font-size:30px;
        //          text-indent:20px;
        //          display:-webkit-box; 		 
        //         -webkit-box-orient:vertical;
        //         text-overflow:ellipsis;
        //         -webkit-line-clamp:2;
        //         overflow:hidden; 
        //         word-break:break-all;
        //         }
          >p{        text-indent:20px;
                    margin-top:20px;
                    font-size:30px;
                    font-family: 宋体;
                     
                }
           >div{
                  display:flex;
              
               .detailimage-first-one{
                   height:110px;
                   i{
                       font-size:30px;
                       position:relative;
                       top:-50px;
                   }
                   span{
                       font-size:100px;
                   }
               }
               .detailimage-first-two{
                   margin-left:30px;
                    height:110px;
                    .detailimage-first-two-one{
                        font-size:30px;
                        position:relative;
                        top:20px;
                       text-decoration: line-through;
                    }
               }

           }
         }
           }
            
 }
</style>


