<template>
  <div class="salemodule-box">
        <div class="salemoduleone">
        <span>————&nbsp;&nbsp;◆&nbsp;</span>
        <span>超值特惠</span>
        <span>&nbsp;◆&nbsp;&nbsp;————</span></div>
        <div  class="salemoduletwo">
            <div :class="'sal'+i"  v-for="(item,i) of sales" :key="(i+1)">
            <div>
                  <p>{{item.title}}</p>
                  <p class="item-vice">{{item.viceTitle.substring(1,item.viceTitle.length-1)}}</p>  
            </div>
            <img :src="item.image" alt="">
          
             </div>
        </div>
        
  </div>


</template>
<script>
export default {
  props: {
    sales: {
      typeof: Array,
      default: []
    }
  }
};
</script>
<style  lang="less">
@import url("../less/less-all.less");
.salemodule {
  &-box {
    height:900px;
    .salemoduleone {
      width: 100%;
      text-align: center;
      margin: 30px auto;
    }
    .salemoduletwo {
      width: 100%;
      //   flex-wrap: wrap;

      //   display:flex;
      border: @border #ccc;
      box-sizing: border-box;
      >div {
        
        text-align: center;
        img {
          width: 240px;
        }
        >div {
          
          text-align: center;
          p {
            margin: 10px auto;
            width: 100%;
          }
          .item-vice {
              color:@pink;
              font-size:18px;/*px*/
          }
        }
      }
  .sal0{
    width:375px;
    border-right: @border #ccc;
    height:498px;
    float: left;
    box-sizing: border-box;
    img{
      width: 300px;
    }
     >div{
      margin-top:40px;
      }
 }
.sal1,
.sal2 {
    width: 370px;
    height:250px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    float: left;
    border-bottom: @border #ccc;
    img{
      width:200px;
      height:200px;
    }
    >div{
      margin-top:40px;
      p{
         margin-top:10px;
      }
    }
  }
.sal3,
.sal4,
.sal5 {
    width:248px;
    box-sizing: border-box;
    float:left;
  }
}


    }
  }
  

  
  
 
// }
</style>


