<template>
   <div class="searchbar-box"> 
         <div class="searchbar-one">
            <div class="searchbar-one-one">
                <input type="text" @focus="flag=true" v-model="searchinput">
                <router-link  :to="'/car'" tag="i" class="iconfont icon-gouwuche"></router-link>
            </div>
            <div class="searchbar-one-two">
                 <div v-for="(item,i) of searchall.sortFilter" :key="i" 
                 :class="{'active':item.sortKey==isactive}"  
                 @click="shousuof(item.sortKey,{fcid:searchbarfcid,title:searchbartitle,page:1,sort:item.sortKey,cKey:16})">
                     {{item.title}}
                </div>
                     
                 <div @click="shousuot">价格 &nbsp;∨</div>
            </div>
            <div class="searchbar-two" v-if="show">
               
            </div>
        </div>
        
   </div>
  
</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
     data(){
         return {
             show:false,
             searchinput:"",
             searchbarfcid:"",
             searchbartitle:"",
             isactive:"pop"
         }
     },
     mounted() {
         this.searchbarfcid=this.$route.query.fcid;
         this.searchbartitle=this.$route.query.title;
         this.loaddatatosearchallthree({fcid:this.searchbarfcid,title:this.searchbartitle,page:1,sort:"pop",cKey:16})
     },
     computed:{
         ...mapState(["searchall"])
     },
     methods:{
         ...mapActions(["loaddatatosearchallthree"]),
         shousuof(e,k){
           this.loaddatatosearchallthree(k);
           this.isactive=e;
         },
         shousuot(){
             this.show=!this.show
         }
     }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
.searchbar{
    &-box{
         width:100%;
         .searchbar-one{
             position: fixed;
             top:0;
             left:0;
             right:0;
            z-index:10;
            background: white;
         .searchbar-one-one{
            
            height:1rem;
            overflow:hidden;
            display:flex;
            padding:0.2rem 0.2rem;
            line-height:0.6rem;
            border-bottom:@border #e5e5e5;
            >input{
            outline: none;
           
            flex-grow: 6;
            border-radius:20px;
            background:url(../../static/search.png) no-repeat 0.15rem center @gray;
            font-size:0.4rem; 
            text-indent: 50px;
             }
             i{
                 flex-grow:1;
                 text-align: center;
                 line-height: 1rem;
                  font-size:0.5rem;
             }
         }
         .searchbar-one-two {
            height:0.6rem;
            overflow:hidden;
            display:flex;
            padding:0.1rem  0;
            border-bottom:@border #e5e5e5;
             .active{
                color:#ff537c;
            }
            div{
                flex-grow:1;
                font-size:0.4rem;
                line-height:0.4rem;
                text-align: center;
                border-left:@border #ccc;
                box-sizing: border-box; 
            } 
         }




        }
        .searchbar-two{
            
            width:100%;
            height:300px;
          
            background: red;
            border-bottom:@border #ccc;
            
        }
    }
}
</style>


