<template>
   <div class="hottype-box">
        <div class="hottypeone">
            <span>————&nbsp;&nbsp;◆&nbsp;</span>
            <span>热门分类</span>
            <span>&nbsp;◆&nbsp;&nbsp;————</span>
        </div>
        <div class="hottypetwo">
         
            <div v-for="(item,i) of hottype" :class="'hot'+i" :key="i"><img :src="item.image" alt=""><p>{{item.title}}</p>  </div>
        </div>
       
   </div>


</template>
<script>
export default {
    props:{
        hottype:{
            typeof:Array,
            default:[]
        }
    }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
.hottype{
    &-box{
        width:100%;
        .hottypeone{
             width: 100%;
             text-align: center;
             margin: 30px auto;
        }
        .hottypetwo{
             width: 100%;
             display:flex;
             flex-wrap:wrap;
           
                div{
                    margin-top:10px;
                    width: 25%;
                    text-align: center;
                    img{
                        width:120px;
                        height:110px;
                    }
                    p{
                       width: 100%;  
                    }
                }

             
        }
    }
}
</style>


