import jsonp from 'fetch-jsonp'
const ParamsObject=function(obj){
     let temp="?"
      for(var i in obj){
         temp += `${i}=${obj[i]}&`
      }
      return temp.replace(/&$/,"")
}

export default {
    request(url,params,opt={}){
     let queryparams=ParamsObject(params)
     return jsonp(url+ queryparams,opt)
          
    }
}


