<template>
     <div class="guesslike-box">
             <div class="guesslikeone">
                  <span>————&nbsp;&nbsp;◆&nbsp;</span>
                  <span>猜你喜欢</span>
                    <span>&nbsp;◆&nbsp;&nbsp;————</span>
        </div>
            <div class="guessliketwo">
                <div v-for="(item,i) of guesslikes" :key="i" @click="sendtodetail(item.tradeItemId,item)">
                 <div class="back" :style="`background:url(${item.img})`"></div>
                 
                     <p>{{item.title}}</p>
                     <div class="guesslike-price">
                         <span class="guessliketwo-one">{{item.price}}</span>
                         <span class="guessliketwo-two">{{item.cfav}}<span class="iconfont icon-shoucang"></span></span>
                      </div>
                     
                      
                  
                 </div>
            </div>
         
         
         
         </div> 
 
</template>
<script>
export default {
    props:{
         guesslikes:{
             typeof:Array,
         } 
    },
    methods:{
        sendtodetail(e,k){
            // console.log(k)
            localStorage.setItem("itemid",JSON.stringify(k));
            this.$router.push({
                path:"/detail",
                query:{
                  itemId:e
                }
                })
        }
    },
    mounted(){
        localStorage.removeItem("itemid")
    }
}    
   
</script>
<style  lang="less">
@import url("../less/less-all.less");
.guesslike{
    &-box{
    .guesslikeone {
      width: 100%;
      text-align: center;
      margin: 30px auto;
    }
    .guessliketwo{
         width:100%;
         display:flex;
         flex-wrap: wrap;
         justify-content: space-around;
         >div{
             margin-top:10px;
             border:@border #ccc;
             width:45%;
             height:540px;
            .back{
              
              width:100%;
              height:400px; 
            }
             p{ 
                 margin-top:10px;
                 width:100%;
                 height:80px;
                 font-size:30px;
                 text-indent:20px;
                 display:-webkit-box; 		 
                -webkit-box-orient:vertical;
                text-overflow:ellipsis;
                -webkit-line-clamp:2;
                overflow:hidden; 
                word-break:break-all;
             }
             .guesslike-price{
                 margin-top:10px;
                 display:flex;
                 justify-content:space-between;
             }
         }
    }
    }
}

</style>


