<template>
   <div class="car-box" ref="car">
        <div class="car-title">
            <li class="iconfont icon-xiangzuojiantou first"></li>
            <div>购物车({{cargoods.length}})</div>
            <li><span>编辑</span><span class="iconfont icon-xiaoxizhongxin"></span></li>
        </div>
       
         <ScrollWrapper class="car-content">
              <GoodsContent :goodscontent="cargoods" />
        </ScrollWrapper> 
        <div class="car-bottom">
              <div class="car-bottom-select">
                  <input type="radio" style="width:30px;height:30px" v-model="sel" @click="isallselect()">
                  <span>全选 ({{len}})</span>
                  <span>{{sum}}</span>
              </div>
              <div class="car-bottom-submit">
                  <input type="button" value="去结算">
              </div>
        </div>
   </div>
</template>
<script>
  import GoodsContent from '../shoppingcomponents/GoodsContent'
export default {
    data(){
        return {
            shoppingcontent:{},
                   cargoods:[],
                   sel:false,
                   flag:false,
                   sum:0,
                   len:0
        }
    },
    components:{
       GoodsContent
    },
    mounted(){
          this.shoppingcontent=JSON.parse(localStorage.getItem("itemid"));
          this.shoppingcontent.ch=false;
          this.cargoods.push(this.shoppingcontent);
    },
    methods:{
          isallselect(){
              this.sel=true;
               this.len=this.cargoods.length;
              for(var i=0;i< this.cargoods.length;i++){
                     this.sum+=this.cargoods[i].price
              }

          }
    },
    beforeRouteEnter(to, from, next) {
        console.log(to)
        let xs=localStorage.getItem("uname");
        if(xs!= 520){
           next(
               {
                   path:"/login",
                   query:{
                       target:to.fullPath
                   }
                   })
        }else{
            next() 
        }
    }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
.car{
   &-box{
    // width:750px;
    background:#f9f9f9;
     
     .car-title{
         width:90%;
         position:fixed;
         left:36px;
        //  right:37;
         top:0;
         display:flex;
         height:80px;
         line-height:80px;
         font-size:50px;
         background:#fafafa;
         border:@border #ccc;
         z-index:10;
          li{
            list-style:none;
            flex-grow: 1;
            text-align: center;
          }
         
         >div{
            text-align: center;
             flex-grow:6;
         }
         span{
             font-size:30px;
             margin-right:10px;
         }

     } 
      .car-content{
         width:90%;
         position:absolute;
         left:36px;
         top:82px;
         bottom:100px;
         background:white;
         overflow: hidden;
          z-index:8;
        //  .car-content-total{
        //      width:100%;
            
        //  }
     } 
     .car-bottom{
         position:fixed;
         bottom:0;
         width:90%;
         display: flex;
         
         left:36px;
         border:@border #ccc;
         box-sizing: border-box;
         height:99px;
         >div{
             line-height: 99px;
             font-size:30px;
         }
         .car-bottom-select{
             margin-left:40px;
             >span{
                 margin-left:10px;
             }
         }
         .car-bottom-submit{
             margin-left:300px;
             input{
                 background: #ff537c;
                 width:150px;
                 height:60px;
                 font-size: 30px;
             }
         }
     }
     }
     
}
</style>


