 <template>
   <div class="loginuser-box">
            <div >
                <input type="button" value="登录" @click="allownext()">
                <input type="button" value="注册">
            </div>
    </div>
 </template>
 <script>
 export default{
     data(){
         return {
            target:""
         }
     },
     methods:{
        allownext(){
            localStorage.setItem("uname",520)
            this.$router.push({path:this.target})
            console.log(this.target)
        },
        beforeRouteEnter (to, from, next) {
            console.log(to)
         next((vm)=>{
             vm.target=to.query.target
         })
        }
     }
 }
 </script>
 <style  lang="less">
 @import url("../less/less-all.less");
   .loginuser{
       &-box{

           div{
             position:absolute;
             top:500px;
             left:0;
             right:0;
             display:flex;
             justify-content:space-around;
             input{
                 width:300px;
                 height:100px; 
                 font-size:50px;
             }
           }
       }
   }
 </style>




