<template>
    <div class="navbar-box">
          <div class="search-box">
              <i class="iconfont icon-xiangzuojiantou" v-show="flag" @click="showsearchs()"></i>
              <i class="iconfont icon-xiaoxizhongxin" v-show="!flag"></i>
              <input type="text" @focus="change"  placeholder="place" v-if="!flag" class="one">
                <input type="text"  v-model="searchinput" placeholder="place" v-if="flag" class="two">
              <i  class="iconfont icon-gouwuche" v-show="!flag" @click="tocar()"></i>
              <div ><li  class="search-text" v-show="flag" @click="searchmsg(searchinput)">搜索</li></div>
          </div>
          <div class="search-histroy" v-if="flag">
               <div class="histroy-one">
                      <div class="histroy-one-one">
                              <span class="iconfont icon-sousuo">&nbsp;历史搜索</span>
                              <i class="iconfont icon-lajixiang" @click="clear"></i>
                      </div>
                      <div class="histroy-one-two">
                             <li v-for="(item,i) of keephistroy" :key="i"  @click="searchsketch(item)" >{{item}} </li>  
                      </div>
               </div>
               <div  class="histroy-two">
                   <div class="histroy-two-one">
                       <span class="iconfont icon-shoucang"> 热门搜索</span>
                   </div>
                   <div  class="histroy-two-two">
                        <div v-for="(item,i) of searchall.hotwords" :key="i" @click="searchsketch(item.query)">
                         <li >{{item.query}}
                        </li>
                       </div>
                   </div>
                     
               </div>
          </div>

    </div>


</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    data(){
        return {
             flag:false,
             searchinput:"",
             place:"包包",
             keephistroy:[],
              
             
        }
    },
    computed:{
        ...mapState(["searchall"])
    },
    mounted(){
        let a = JSON.parse(localStorage.getItem("mess"))
    //    this.keephistroy=JSON.parse(localStorage.getItem("mess"))
    let keep=[]
       if(a!=null)
        for(var i=0;i<a.length;i++){
            if(keep.indexOf(a[i])==-1)
            keep.push(a[i])
        }
       this.keephistroy=keep;
      
      
    },
    methods:{
        ...mapActions(["loaddatatosearchallone"]),
        ...mapActions(["loaddatatosearchalltwo"]),
        ...mapActions(["loaddatatosearchallthree"]),
        change(){
          this.loaddatatosearchallone();
           this.flag=true;
        },
        showsearchs(){
            this.flag=false;
        },
        searchmsg(k){
           
            this.loaddatatosearchallthree({q:k,cKey:46,page:1,sort:"pop"});
            
           console.log(k) 
           this.keephistroy.push(k);
           
         
           localStorage.setItem("mess",JSON.stringify(this.keephistroy));
           this.place=k;
           this.$router.push({
               path:"/search"
              })
        },
        searchsketch(k){
                //  this.searchinput=k;
                 this.keephistroy.push(k);
                 localStorage.setItem("mess",JSON.stringify(this.keephistroy)); 
              this.loaddatatosearchallthree({q:k,cKey:46,page:1,sort:"pop"});
              this.$router.push({
               path:"/search"
              })
        },
        clear(){
              localStorage.removeItem("mess")
        },
        tocar(){
            this.$router.push({
               path:"/car"
              })
        }

    }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
 .navbar{
     &-box{
       .search-box{
        position:fixed;
        top:0;
        left:0;
        right:0;
        height:1rem;
         line-height: 1rem;
        overflow:hidden;
        display:flex;
        padding:0.2rem 0.2rem;
        z-index:99;
        border-bottom:@border #e5e5e5;
        // box-sizing:border-box;
        >i{
            flex-grow: 1;
            text-align: center;
           
            font-size:0.6rem;
        }
         .one{
            text-indent: 50px;
            outline:none;
            margin-top:10px;
            height:0.6rem;
            flex-grow:7;
            border-radius:0.2rem;
            background:url(../../static/search.png) no-repeat 0.15rem center ;
            font-size:0.4rem;
            background-size: 6%;
        }
          .two{
            text-indent: 50px;
            outline:none;
            margin-top:10px;
            height:0.6rem;
            flex-grow:7;
            border-radius:0.2rem;
            
             background-size: 6%;
            background:url(../../static/search.png) no-repeat 0.15rem center  ;
            font-size:0.4rem;
        }
        .two:hover{
             border:1.5px solid @red;
        }
          
        .search-text{
            font-size: 0.5rem;
            font-family:宋体;
            list-style:none;
        }
       } 
      .search-histroy{
          position:absolute;
          top:105px;
          left:0;
          right:0;
          bottom:0;
          z-index:80;
          background: white;
          .histroy-one{
              width:750px;
              border-bottom: @border #ccc;
              .histroy-one-two{
                  margin-top:15px;
                  width:100%;
                  display:flex;
                  flex-wrap: wrap;
                  margin-bottom: 15px;
                  margin-left:15px;
                  font-size:40px;
                  li{
                     list-style: none;
                     margin-top:10px;
                     margin-left:10px;
                      font-family: 宋体;
                  }
              }
              .histroy-one-one{
                  width:100%;
                  height:100px;
                  line-height: 100px;
                  display:flex;
                  justify-content:space-around;
                  border-bottom: @border #ccc;

              }
          }
          .histroy-two{
              width:750px;
              
              margin-top:20px;
               .histroy-two-one{
                   width:100%;
                   height:60px;
    
                   margin-bottom:20px;
                   border-bottom:@border #ccc;
                   >span{
                    margin-left:20px;
                   font-size:40px;
                   line-height:60px;
                   }
               }
               .histroy-two-two{
                >div{
                  margin:20px;
                  
                  border:@border #ccc;
                  border-radius: 2px;
                  background:white;
                  text-align: center;
                  float: left;
                  >li{
                      margin:15px;
                      font-family: 宋体;
                      list-style: none;
                  }
              }
               }
              
          }
      }
     }
 }
</style>


