<template>
    <div class="goodscontent-box">
          <div v-for="(item,i) of goodscontent" :key="i">
              <div class="goods-select"><input type="radio" style="width:30px;height:30px" v-model="item.ch"></div>
              <div class="goods-img"><img :src="item.img" alt=""></div>
              <div class="goods-content">
                        <p> {{item.title}}</p>
                        <span class="nowprice">&#65509;{{item.price}}</span>
                        <span class="oldprice">&#65509;{{item.orgPrice}}</span>
              </div>

          </div>
    </div>
</template>
<script>
export default {
    props:{
        // ch:{type:Boolean,dedault:false},
        goodscontent:{
           type:Array,
           default:[]
       }
    }
}
    
</script>
<style  lang="less">
@import url("../less/less-all.less");
.goodscontent{
    &-box{
        width:100%;
       >div{
           display:flex;
           div{
             
               margin-top:20px;
               margin-bottom:20px;
               height:150px;
           }
           .goods-select{
             width:150px;
             line-height: 150px;
               text-align: center;
           }
           .goods-img{
              width:200px;
                >img{
                    width:100%;
                    height:100%;
                }
           }
           .goods-content{
               width:350;
               margin-left:20px;
                p{
                   text-indent:20px; 
                }
                .nowprice{
                   margin-left:40px;
                   font-size:30px;
                }
                .oldprice{
                    margin-left:20px;
                    font-size:20px;
                    color:#ccc;
                    text-decoration: line-through
                }
           }

       }
    }
}
</style>


