import api from "../../sever/api"
const state={
    hotwords:[],
     sketchs:[],
     allcontent:[],
     sortFilter:[],
     searchFcid:"" ,
     assesses:{}
}
const actions={
    loaddatatosearchallone({commit}){
        api.loadDataByhot({platform:"H5",code:"hotWord"
            },(data)=>{
            // console.log(data);
            commit("sethotWords",data.data.hotWord.data)
        });
      
       
    },
    loaddatatosearchalltwo({commit}){

        api.loadDataByhot({platform:"H5",code:"sketch"},(data)=>{
            commit("setSkrtchs",data.data["117330"].list)
        });
    },
    loaddatatosearchallthree({commit},e){
           
        api.loadDataBykeyword(e,(data)=>{
            console.log(data)
            commit("setAllContent",data.result.wall.docs)
            commit("setSortFilter",data.result.sortFilter)
            commit("setSearchFcid",data.result.param.fcid)
        });
    },
    loadDataTodetail({commit},e){
        api.loadDataTodetailByAssess(e,(data)=>{
            console.log(data.data.list)
            commit("setAssesses",data.data)
        })
    }
   
   

}
const mutations={
    sethotWords(state,data){
        state.hotwords=data;
    },
    setSkrtchs(state,data){
    
        state.sketchs=data
    },
    setAllContent(state,data){
        state.allcontent=data
    },
    setSortFilter(state,data){
        state.sortFilter=data
    },
    setSearchFcid(state,data){
        state.searchFcid=data
    },
    setAssesses(state,data){
        state.assesses=data
    }
}
export default {
    state,
    actions,
    mutations
}


