<template>
    <div class="rightnav-box">
         <div class="rightnav-title">
           <div v-for="(item,i) of classpage.rightnaves" :key="i"  @click="matchfcid(item.title,item.link)">
                <img :src="item.image" alt="">
                <p>{{item.title}}</p>

            </div>
           </div>
        <div class="rightnav-type">
            <div v-for="(item,i) of searchall.sortFilter" :key="i"
             :class="{'active':item.sortKey==isactive}" 
             @click="shousuof(item.sortKey,{fcid:searchall.searchFcid,page:1,sort:item.sortKey,cKey:'h5-cube'})">{{item.title}}</div>
                    <!-- <div  @click="shousuof">综合</div>
                    <div @click="shousuof">销量</div>
                    <div @click="shousuof">新品</div>  -->


        </div>  
        <div class="rightnav-detail">
              <div v-for="(item,i) of searchall.allcontent" :key="i"  @click="sendtodetail(item.tradeItemId,item)">
                   <div class="rightnav-back" :style="`background:url(${item.img})`"></div>
                 
                     <p >{{item.title}}</p>
                     <div class="rightnav-price">
                         <span class="rightnav-one">{{item.price}}</span>
                         <span class="rightnav-two">{{item.cfav}}<span class="iconfont icon-shoucang"></span></span>
                      </div>
                     
                      
                  
              </div>


              </div>
        
    </div>
</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    data(){
        return{
           searchfcid:"",
           isactive:"pop"
        }
    },
    computed:{
     ...mapState(["classpage"]),
     ...mapState(["searchall"])
     },
     mounted(){
          localStorage.removeItem("itemid")
     },
     methods:{
    ...mapActions(["loaddatatosearchallthree"]),
      matchfcid(title,link){
          this.searchfcid =link.substring(link.indexOf("=")+1,link.indexOf("&"))
          this.$router.push('/search?fcid='+this.searchfcid+'&title='+title)
    },
      shousuof(e,k){
          this.loaddatatosearchallthree(k)
          this.isactive=e;
      },
      sendtodetail(e,k){
           localStorage.setItem("itemid",JSON.stringify(k));
            this.$router.push({
                path:"/detail",
                query:{
                  itemId:e
                }
                })
      }
     }
}

</script>
<style lang="less">
@import url("../less/less-all.less");
.rightnav{
    &-box{
        width:100%;
        .rightnav-title{
            width:100%;
            display:flex;
            justify-content: space-around;
            flex-wrap: wrap;
            >div{
               width:32%;
               text-align:center;
               margin-top:20px;
               img{
                   width:150px;
                   height:150px;
               }
            }
        }
        .rightnav-type{
            margin-top:30px;
            height:0.6rem;
            overflow:hidden;
            display:flex;
            padding:0.1rem  0;
            border-bottom:@border #e5e5e5;
            border-top:@border #e5e5e5;
            .active{
                color:#ff537c;
            }
            div{
                flex-grow:1;
                font-size:0.4rem;
                line-height:0.4rem;
                text-align: center;
                border-left:@border #ccc;
                box-sizing: border-box; 
            } 
        }
        .rightnav-detail{
             width:100%;
            >div{
             float: left;
             margin-top:15px;
             margin-left:20px;
             border:@border #ccc;
             width:45%;
             height:520px;
             .rightnav-back{
                 width:100%;
                 height:380px;
               
             }
              p{ 
                 margin-top:10px;
                 
                 width:100%;
                 height:80px;
                 font-size:30px;
                 text-indent:20px;
                 display:-webkit-box; 		 
                -webkit-box-orient:vertical;
                text-overflow:ellipsis;
                -webkit-line-clamp:2;
                overflow:hidden; 
                word-break:break-all;
             }
             .rightnav-price{
                 margin-top:10px;
                 display:flex;
                 justify-content:space-between;
             }
        }
             
        }
    .rightnav-detail:after {
            content: "";
            clear: both;
            display: block;
}
    }
}
</style>



