import api from "../../sever/api"
const state= {
    swiperSlides:[],
    allmenu:[],
    allsales:[],
    allhottype:[],
    guesslike:[]
}
const actions= {
     loaddatatohome({commit}){
            api.loadDataByPid([122224,106930,51833,51836],(data)=>{
                    commit("setSwiperSlides",data.data["122224"].list)
                    commit("setAllmenu",data.data["106930"].list)
                    commit("setAllsales",data.data["51833"].list)
                    commit("setAllhottype",data.data["51836"].list)
                });
          api.loadDataToLike({pid:"9750",sort:"pop",cKey:46},(data)=>{
                commit("setGuesslike",data.result.wall.docs)})
     }
     
}
const mutations={
   setSwiperSlides(state,data){
    state.swiperSlides=data
   },
   
   setAllmenu(state,data){state.allmenu=data} ,
   setAllsales(state,data){state.allsales=data} ,
   setAllhottype(state,data){state.allhottype=data} ,
   setGuesslike(state,data){ state.guesslike=data}
}
export default {
    state,
    actions,
    mutations
}


