<template>
   <div class="classpage-box"   >
       <NavBar/>
       <div class="classpagebody">
       <ScrollWrapper class="left-classpage">
           <LeftNav :leftnavs="classpage.leftnavs"/>
       </ScrollWrapper>
       <ScrollWrapper class="right-classpage">
           <RightNav/>
       </ScrollWrapper>    
       </div>
     
    <BottomBar/>
   </div>
</template>
<script>
import NavBar from "../shoppingcomponents/NavBar" 
import BottomBar from "../shoppingcomponents/BottomBar"
import LeftNav from "../shoppingcomponents/LeftNav"
import RightNav from "../shoppingcomponents/RightNav"
 
import { mapState,mapActions,mapMutations } from 'vuex'
  export default {
      data(){
          return {
             
             info:"",

          }
      },
      components:{
         NavBar,BottomBar,RightNav,LeftNav
      },
      computed:{
          ...mapState(["classpage"])
      },
      mounted(){
          this.loaddatatoclasspage()
      },
      methods:{
          ...mapActions(["loaddatatoclasspage"])
        }
      }

  
</script>
<style  lang="less">
@import url("../less/less-all.less");
.classpage {
    &-box {
          
         .classpagebody{ 
           overflow:hidden;
           position:absolute;
           top:105px;
           left:0;
           right:0;
           bottom:1.2rem;
           background:white;
           display:flex;
           justify-content: space-between;
           .left-classpage{
              width:25%;
              height:100%;
              background:@gray;
              
           }
           .right-classpage{
              width:75%;
              height:100%;
              background:#fff;
           }
   }
          

          
        div {
          
               
           
    
     
        }
    }
}
</style>


