<template>
   <div class="home-box"  >
       <NavBar/>
       
       <ScrollWrapper class="homebody"> 
          <Swipers :swiperSlides="homepage.swiperSlides"/>
          <NaviGation :menu="homepage.allmenu"/>
          <SaleModule :sales="homepage.allsales"/>
          <HotType :hottype="homepage.allhottype"/>
          <GuessLike :guesslikes="homepage.guesslike"/>
       </ScrollWrapper>
       
       <!-- <NaviGation  :/> -->
    <BottomBar/>
   </div>
</template>
<script>
import NavBar from "../shoppingcomponents/NavBar";
import BottomBar from "../shoppingcomponents/BottomBar";
import NaviGation from "../shoppingcomponents/NaviGation"
import Swipers from "../shoppingcomponents/Swipers";
import SaleModule from "../shoppingcomponents/SaleModule"
import HotType from "../shoppingcomponents/HotType"
import GuessLike from "../shoppingcomponents/GuessLike"
// import api  from '../sever/api'
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
  data() {
    return {
      
    } 
},
  components: {
    Swipers,
    NavBar,
    BottomBar,
    NaviGation,
    SaleModule,
    HotType,
    GuessLike
  },
  computed:{
     ...mapState(["homepage"])

  },
  methods:{
    ...mapActions(["loaddatatohome"])
  },
  mounted(){
    this.loaddatatohome() 
          
}


          
    }

</script>
<style  lang="less">
@import url("../less/less-all.less");
.home {
  &-box {
    .homebody {
      overflow: hidden;
      position: absolute;
      top: 105px;
      left: 0;
      right: 0;
      bottom: 1.2rem;
    //   background: violet;
    }

     
  }
}
</style>


