<template>
   <div  class="swipers-box">
       <swiper :options="swiperOption" ref="mySwiper">
         <swiper-slide v-for="(item,i) of swiperSlides" :key="i"><img :src="item.image_800" alt=""></swiper-slide>     
        <div class="swiper-pagination"  slot="pagination"></div>
       </swiper> 
    


   </div>

</template>

<script>
// import 'swiper/dist/css/swiper.css'
export default {
  data() {
    return {
    
      
      swiperOption: {
        autoplay:true,
        // autoplay: {
        //     disabledOninteraction:false
        // },
        // initialSlide: 1,
        speed: 400,
        delay: 200,
        loop: true,
        pagination: ".swiper-pagination"
        
      }
    };
  },
   props:{
          swiperSlides:{typeof:Array,default:[]}
      },
  mounted() {
    //   this.$axios.get().then((res)=>{
    //       this.map=res
    //   })
    //     setInterval(() => {
    //     if (this.swiperSlides.length < 4) {
    //       this.swiperSlides.push(this.swiperSlides.length + 1)
    //     }
    //   }, 1000)
  }
};
</script>
<style  lang="less">
@import url("../less/less-all.less");
.swipers {
  &-box {
    width: 100%;
    // margin-top: 10px;
    height: 330px;
    overflow: hidden;
   
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: 60px;
      left: 10px;
      width: 100%;
      text-align: left;
      .swiper-pagination-bullet {
        width: 16px;
        height: 16px;
        display: inline-block;
        border-radius: 100%;
        background: #000;
        opacity: 0.2;
      }
      .swiper-pagination-bullet-active {
        opacity: 1;
        background: white;
      }
    }
  }
}
</style>


