import Vue from 'vue'
import Router from 'vue-router'
import HomePage from "../pages/HomePage"
import ClassPage from "../pages/ClassPage"
import CarPage from "../pages/CarPage"
import MyPage from "../pages/MyPage"
import UnFounted from "../pages/UnFounted"
import SearchAll from "../pages/SearchAll"
import Detaillist from "../pages/DetailList"
import LoginUser from "../pages/LoginUser"
Vue.use(Router)

export default new Router({
    linkActiveClass:"active",
    linkExactActiveClass:"exactActive",
    mode:"history",
    routes:[ 
        {path:"/",redirect:"home"}, 
        {path:"/home",component:HomePage 
            // {path:"",redirect:"booklist"},
            // {path:"booklist",component:LunImg},
            // {path:"bookdetail",component:BookDetail}
        },
        {path:"/detail",component:Detaillist
        },
        {
          path:"/login",component:LoginUser
        },
        {path:"/search",component:SearchAll},
        {path:"/class",component:ClassPage},
        {path:"/my",component:MyPage},
        {path:"/car",component:CarPage},
        {path:"*",component:UnFounted}
    ]
})



