<template>
    <div class="searchscr-box">
          <div v-for="(item,i) of searchall.allcontent" :key="i"  @click="sendtodetail(item.tradeItemId,item)">
                 <div class="searchscr-back" :style="`background:url(${item.img})`"></div>
                 
                     <p>{{item.title}}</p>
                     <div class="searchscroll-price">
                         <span class="searchscrolltwo-one">{{item.price}}</span>
                         <span class="searchscrolltwo-two">{{item.cfav}}<span class="iconfont icon-shoucang"></span></span>
                      </div>
                     
                      
                  
                 </div>
    </div>

 
</template>
<script>
  import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    computed:{
        ...mapState(["searchall"])
    },
    methods:{
          sendtodetail(e,k){
           localStorage.setItem("itemid",JSON.stringify(k));
            this.$router.push({
                path:"/detail",
                query:{
                  itemId:e
                }
                })
      }
        // ...mapActions(["loaddatatosearchallthree"])
    },
    mounted(){
         localStorage.removeItem("itemid")
    }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
.searchscr{
    &-box{
        width:100%;
        >div{
             float: left;
             margin-top:15px;
             margin-left:20px;
             border:@border #ccc;
             width:45%;
             height:600px;
             .searchscr-back{
                 width:100%;
                 height:460px;
               
             }
              p{ 
                 margin-top:10px;
                 
                 width:100%;
                 height:80px;
                 font-size:30px;
                 text-indent:20px;
                 display:-webkit-box; 		 
                -webkit-box-orient:vertical;
                text-overflow:ellipsis;
                -webkit-line-clamp:2;
                overflow:hidden; 
                word-break:break-all;
             }
             .searchscroll-price{
                 margin-top:10px;
                 display:flex;
                 justify-content:space-between;
             }
        }
    }
}
 .searchscr-box:after {
            content: "";
            clear: both;
            display: block;
}
</style>


