<template>
    <div class="searchall-box">
        <SearchBar />
        <ScrollWrapper class="search-content">
            <SearchScroll/>
        </ScrollWrapper>
    </div>
</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex';
import SearchBar from "../shoppingcomponents/SearchBar"; 
import SearchScroll from "../shoppingcomponents/SearchScroll"
export default {
       components:{
           SearchBar,
           SearchScroll
       },
       computed:{
            // ...mapState(["searchall"])
       }
}
</script>
<style lang="less">
@import url("../less/less-all.less");
.searchall{
    &-box{
       .search-content{
            overflow: hidden;
            position: absolute;
            top: 169px;
            left: 0;
            right: 0;
            bottom: 0rem;
           background: white;
           z-index:8;
       } 
      
    }
}
</style>



