import api from "../../sever/api"
const state={
    leftnavs:[],
    rightnaves:[] 
}
const actions={
    loaddatatoclasspage({commit}){
        api.loadDataByPid("117330",(data)=>{
            
            commit("setLeftnavs",data.data["117330"].list)
        });
       
    },
    loaddatatoclassright({commit},e){
         api.loadDataByLeftPid(e,(data)=>{
             console.log(data)
        commit("setRightnavs",data.data.list)

    })  
    } 
    

}
const mutations={
    setLeftnavs(state,data){
        state.leftnavs=data;
    },
    setRightnavs(state,data){
    
        state.rightnaves=data
    }
}
export default {
    state,
    actions,
    mutations
}


