<template>
      <div class="assess-box">
            <div class="assess-total">
                <span>买家评价:{{detailcomments.total}} |</span>
                <span>></span>
            </div>
            <div class="assess-hot">
                <span v-for="(item,i) of detailcomments.rateScore" :key="i">{{item.key}}</span>
            </div>
            <div v-for="(item,i) of detailcomments.list" :key="i" class="assess-list">
                <div class="assess-user">
                    <img :src="item.user.avatar" alt="">
                    <span>{{item.user.uname}}</span>
                </div>
                <div class="assess-content">
                    {{item.content}}
                </div>
                <p>{{item.time}}</p>
            </div>
      </div>
    
</template>
<script>
export default {
    props:{
        detailcomments:{
            type:Object,
            default:{}
        }
    }
}

</script>
<style  lang="less">
@import url("../less/less-all.less");
.assess{
    &-box{
      margin-top:20px;
       background:white; 
        padding:20px 30px 20px 25px;
        width:92%; 
       .assess-total {
           font-size:30px;
           display:flex;
           justify-content:space-between;
           margin-bottom:10px;
        }
        .assess-hot{
            font-size:30px;
            >span{
                background:#ffe6e8;
              color:@red;
                margin-left:10px;
            }
        }
        .assess-list{
            margin-top:20px;
           .assess-user{
               margin-top:10px;
               img{
                   width:60px;
                   margin-right:10px;
               }
               >span{
                   font-size:30px;
               }
           } 
           .assess-content{
               margin-top:10px;
               text-indent: 20px;
           }
           >p{
               margin-top:10px;
               font-size:30px;
           }
        }

    }
}
</style>


