import jsonpUtil from '../util/jsonpUtil'
const mce_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_seach = "https://list.mogujie.com/search";
const right_class="https://mce.mogucdn.com/jsonp/get/3"
const search_all="https://list.mogujie.com/search"
const search_hot="https://list.mogujie.com/module/mget"
const detail_assess="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1"
const paramsp={
    appPlat:"m"
}
const paramsr={
    appPlat:"m"
}
const paramst={

}
const error=function(e){
    console.log(e);
    //  alert("请求失败")
}
export default {
     loadDataByPid(pids,success=()=>{}){
         if(!pids){
             alert("参数传递有误")
         }
         if(!Array.isArray(pids)){
            paramsp.pids=pids
         } 
         if(Array.isArray(pids)){
            let temp =""
            for(var i in pids){
                temp +=pids[i]+","
            }
            paramsp.pids=temp.replace(/,$/,"")
         }
         jsonpUtil.request(mce_multiget,paramsp)
                .then((res)=>{
                    if(res.ok){
                        return res.json();
                    }else{
                        throw new Error("请求失败");
                    }
                }).then(success)
                  .catch(error)
        
                 
     },
     loadDataToLike(params,success=()=>{}){
        jsonpUtil.request(list_seach,params)
        .then((res)=>{
            if(res.ok){
                return res.json();
            }else{
                throw new Error("请求失败");
            }
        }).then(success)
          .catch(error)
     },
     loadDataByLeftPid(params,success=()=>{}){
        paramsr.pid=params;
         
        jsonpUtil.request(right_class,paramsr)
        .then((res)=>{
            
                return res.json();
           
        }).then(success)
          .catch(error)
     },
     loadDataByhot(params,success=()=>{}){
         
        jsonpUtil.request(search_hot,params)
        .then((res)=>{
            
                return res.json();
            
        }).then(success)
          .catch(error)
     },
     loadDataBykeyword(params,success=()=>{}){
      
       jsonpUtil.request(search_all,params)
       .then((res)=>{
           
               return res.json();
           
       }).then(success)
         .catch(error)
    },
    loadDataTodetailByAssess(params,success=()=>{}){
        console.log(params)
        jsonpUtil.request(detail_assess,params)
        .then((res)=>{
            if(res.ok){
                return res.json();
            }else{
                throw new Error("请求失败");
            }
        }).then(success)
          .catch(error)
    }

}


