<template>
      <div class="detaillist-box">
        <ScrollWrapper class="detaillist-all">
           <DetailImage :detailimages="detailcontent" :detailassess="searchall.assesses"/>
       </ScrollWrapper>
      <DetailBottom/>
       
      </div>

</template>
<script>
import DetailImage from "../shoppingcomponents/DetailImage"
import DetailBottom from "../shoppingcomponents/DetailBottom"
import { mapState,mapActions,mapMutations } from 'vuex';
  export default {
    data(){
        return {
            detailcontent:{}
        }
    },
    computed:{
       ...mapState(["searchall"])
    },
    components:{
      DetailImage,DetailBottom
    },
    mounted(){
          this.detailcontent=JSON.parse(localStorage.getItem("itemid"))
          console.log( this.detailcontent);
          this.loadDataTodetail({type:1,itemId:this.$route.query.itemId,appPlat:"m"})
         
     },
     methods:{
       ...mapActions(["loadDataTodetail"])

     }
  }


</script>
<style  lang="less">
@import url("../less/less-all.less");
.detaillist{
    &-box{
     .detaillist-all{
         position:absolute;
         top:0;
         left:0;
         right:0;
         bottom:100px;
         background:#ccc;
     }
    }
}



</style>


