<template>
     <div class="liftnav-box">
           <div v-for="(item,i) of leftnavs" :key="i" :class="{'active':item.maitKey==isactive}"  @click="sendmsg(item.maitKey,item.miniWallkey)">
               <p>{{item.title}}</p>
           </div>
     </div>
</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    data(){
        return {
            isactive:3627,
        }
    },
     props:{
         leftnavs:{
             typeof:Array,
             default:[]
         }
     },
     mounted(){
             this.loaddatatoclassright("3627")
             this.loaddatatosearchallthree({fcid:3627,page:1,sort:"pop",cKey:"h5-cube"})
         },
     methods:{
         ...mapActions(["loaddatatoclassright"]),
        ...mapActions(["loaddatatosearchallthree"]),
         sendmsg(pid,miniWallkey){
             
             this.loaddatatoclassright(pid);
             this.isactive=pid;
             this.loaddatatosearchallthree({fcid:miniWallkey,page:1,sort:"pop",cKey:"h5-cube"})
         },
        
     }
}
</script>
<style  lang="less">
@import url("../less/less-all.less");
.liftnav{
    &-box{
        width:100%;
        >div{
          width:100%;
          height:80px; 
        //   line-height:100px;
         
          padding-top:20px;
          font-size: 40px;
          text-align: center;
          font-family: 宋体;
        
          p{
              margin-left:10px;
            //   margin-top:20px;
              width:90%;
              line-height:50px;
              height:50px;
              border-left:8px solid transparent;
              box-sizing: border-box;
          }
        }
        .active{
              background:white;
              >p{
                  color:#ff537c;
                  border-left:8px solid #ff537c;
              }
          }
    }
       
}
</style>


