<template>
  <div class="navigation-box">
      
      <div v-for="(item,i) of menu" :key="'menuitem'+i">
          <img :src="item.image" alt="">
          <p>{{item.title}}</p>

      </div>
    

  </div>



</template>
<script>

export default{
    props:{
         menu:{
             typeof:Array,
             default:[]
         }
    },
    data(){
        return{
            items:[]
        }
    }

}
</script>
<style  lang="less">
@import url("../less/less-all.less");
 .navigation{
     &-box{
         margin-top:10px;
         width: 100%;
         flex-wrap: wrap;
         display: flex;
         overflow: hidden;
         >div{
             width:150px;
             text-align: center;
             img{
                 width:80px;
                 height:80px;
             }
         }
     }
 }


</style>


