<template>
<div class="detailbottom-box">
     <div> <i class="iconfont icon-shoucang"></i><p>收藏</p> </div>
     <div> <i class="iconfont icon-shouye"></i><p>小店</p> </div>
     <div @click="entercarpage()"> 加入购物车</div>
     <div> 立即购买 </div>

</div>
</template>
<script>
   export default {

       methods:{
           entercarpage(){
               this.$router.push({
                   path:"/car",
                   query:{
                      params:this.$route.query.itemId
                   }
                    
                   })
           }
       }
   }
</script>
<style  lang="less">
@import url("../less/less-all.less");
.detailbottom{
    &-box{
        position:fixed; 
        left:0;
        right:0;
        bottom:0px;
       
        display:flex;
        height:100px;
        z-index:99;
        div:nth-child(1){
             i{
                 margin-top:10px;
             }
              flex-grow: 1;
              text-align: center;
              height:99px;
              border-top:@border #ccc;
              border-right:@border #ccc;
            //   line-height: 99px;
              background: white;
              p{
                  padding:0;
              }
        }
         div:nth-child(2){
               i{
                 margin-top:10px;
             }
              flex-grow: 1;
              text-align: center;
              height:99px;
              border-top:@border #ccc;
              border-right:@border #ccc;
            //   line-height: 99px;
             background: white;
             p{
                 padding:0;
             }
        }
        div:nth-child(3){
              flex-grow: 2;
              text-align: center;
              height:100px;
              background:#ffe6e8;
              color:@red;
              line-height: 100px;
        }
        div:nth-child(4){
              flex-grow: 2;
              text-align: center;
              line-height: 100px;
              height:100px;
              background:#ff537c;
              color:@white;
        }
    }
}
</style>


