import Vue from 'vue'
import Vuex from 'vuex'
import homepage from './moduels/homepage'
import classpage from './moduels/classpage.js'
import searchall from './moduels/seachall.js'
Vue.use(Vuex)
export default new Vuex.Store({
      modules:{
        homepage,
        classpage,
        searchall
      }
})


